import React, { useState, useEffect } from 'react';

import './PageSolutions.css';
import Zoom from 'react-reveal/Zoom';

import axios from 'axios';

function PageSolutions(props) {

	const [solutions, setSolutions] = useState([]);
	useEffect(() => {
		axios({
			method: 'GET',
			url: 'http://192.168.0.10:8000/api/solutions'
		}).then((response) => {
			setSolutions(response.data);
		});
	}, []);

	return (
		<Zoom>
			<div className="PageSolutions">
				{/* <h3>Решения</h3> */}
				{solutions.map((c) => (c.id === 3)?(<h3>{c.name}</h3>) : null)}
				<br />
				Мы предлагаем комплекс работ по проектированию, монтажу, настройке и обслуживанию: систем управления
				инженерными системами: освещением, климатом на оборудовании KNX (EIB), управление освещением на
				оборудовании Lutron HomeWorks; централизованных систем управления всем комплексом инженерного и
				мультимедийного оборудования (Crestron); АСУ инженерными системами и диспетчеризация инженерных систем;
				систем электроснабжения квартир и коттеджей, включая вводные распределительные устройства,
				дизель-генераторные установки, молниезащиту и обогрев кровли; аудио-видео оборудования домашнего
				кинотеатра любого уровня с разработкой проекта акустической подготовки помещения.
			</div>
		</Zoom>
	);
}

export default PageSolutions;
